.btn {
  position: fixed;
  z-index: 1;
  bottom: 50px;
  right: 50px;
  background-image: url(https://cdn-icons-png.flaticon.com/512/1028/1028163.png);
  background-size: contain;
  border-radius: 50%;
  border: 2px rgb(4, 233, 16) solid;
  width: 100px;
  height: 100px;
  transition-duration: 1s;
}

.btn:hover {
  transform: scale(1.3);
  transition-duration: 1s;
  cursor: pointer;
}
