form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(160, 158, 158);
  width: 400px;
  height: 200px;
  border-radius: 20px;
  border: rgb(0, 204, 255) 1px solid;
}

textarea {
  width: 360px;
  height: 100px;
  margin: 10px;
}

.btn-form {
  margin-left: 10px;
  margin-right: 10px;
  width: 80px;
  height: 30px;
  border-radius: 10px;
  border: none;
  color: white;
}

.btn-form-cancel {
  background: rgb(248, 65, 65);
}

.btn-form-add {
  background: rgb(13, 148, 13);
}
